import React from 'react';
import About from './About';
import Contact from './Contact';
import Header from './Header';
import Main from './Main';
import Gallery from './Gallery';
import Footer from './Footer';
import LogoData from './LogoData';
import ArtData from './ArtData';

import {
  BrowserRouter,
  Switch,
  Route
} from "react-router-dom";

function App() {
  return (
    <div>
      <BrowserRouter>
        <Switch>
          {/* If the current URL is /about, this route is rendered
              while the rest are ignored */}
          <Route path="/art-gallery">
            <Header />
            <Gallery logodata={ArtData} />
            <Footer />
          </Route>

          <Route path="/logo-gallery">
            <Header />
            <Gallery logodata={LogoData} />
            <Footer />
          </Route>

          <Route path="/art">
            <Header />
            <Main type={"art"} logodata={ArtData} maxsize={2}/>
            <Footer />
          </Route>

          <Route path="/about">
            <Header />
            <About />
            <Footer />
          </Route>

          <Route path="/contact">
            <Header />
            <Contact />
            <Footer />
          </Route>

          <Route path="/">
            <Header />
            <Main type={"logo"} logodata={LogoData} maxsize={3}/>
            <Footer />
          </Route>
        </Switch>
      </BrowserRouter>
    </div>
  );
}

export default App
