import React from 'react'
import Logo from './Logo'



class Logos extends React.Component {

  constructor(props) {
    super(props);
    this.state = { selections: []};
    this.buildChildren = this.buildChildren.bind(this);
  }

  onChildToggle(id, selected) {
    var selections = [];
    selections[id] = selected;

    var indexNum = this.props.logodata.findIndex(obj => obj.name === id);

    console.log("Logo with id " + id + " and index " + indexNum + " is selected ");

    this.setState({
      selections: selections,
    });
  }

  buildChildren(logo, index, size) {
    return <Logo key={logo.name} type={this.props.type} logo={logo} maxsize={this.props.maxsize} selected={this.state.selections[logo.name]} handleClick={this.onChildToggle.bind(this)} />
  }


  render() {
    return  <div className="pure-g"> {this.props.logodata.map(this.buildChildren)} </div>
  }
}

export default Logos;
