import React from 'react'

class LogoOverlay extends React.Component {
  getClassNames() {
    return this.props.enable ? "after pure-u-1 enable" : "after pure-u-1"
  }

  render() {
    return (
      <div className={this.getClassNames()}>{this.props.text}</div>
    )
  }
}

export default LogoOverlay;
