import React from 'react'
import LogoOverlay from './LogoOverlay'
import {
  Link
} from "react-router-dom";

class Logo extends React.Component {

  constructor(props) {
    super(props);
    this.img = props.logo.image;
    this.name = props.logo.name;
    this.type = props.type;
    console.log(this.type);
    console.log(props);

    // This binding is necessary to make `this` work in the callback
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick() {
    this.props.handleClick(this.props.logo.name, !this.props.selected);
  }

  getGridSizeClasses() {
    return "pure-u-sm-1 pure-u-md-1-2 pure-u-lg-1-" + this.props.maxsize + " logo-container" ;
  }

  render() {
    return (
      <div className={this.getGridSizeClasses()}>
        <div  onClick={this.handleClick}  className="logo-wrapper">
          <div className="logo-small">
            <Link to={"/" + this.type + "-gallery?focus=" + this.name}>
              <img className="pure-img" src={require(`${this.img}`)} alt={this.name}></img>
              <LogoOverlay text={this.name}/>
            </Link>
          </div>
        </div>
      </div>
    )
  }
}

export default Logo;
