import React from "react";

export default class ContactForm extends React.Component {
  constructor(props) {
    super(props);
    this.submitForm = this.submitForm.bind(this);
    this.state = {
      status: ""
    };
  }

  render() {
    const { status } = this.state;
    return (
      <div>
        <form
          className="pure-form pure-form-stacked"
          onSubmit={this.submitForm}
          action="https://formspree.io/xlepgnvg"
          method="POST"
        >
          <fieldset>
            <div className="pure-g">
              <div className="pure-u-1">
                <label for="name">Name:</label>
                <input className="pure-input-rounded" type="text" id="name" name="name"/>
                <label>Email:</label>
                <input className="pure-input-rounded" type="email" name="email" />
              </div>
              <div className="pure-u-1">
                <label>Message:</label>
                <textarea className="pure-input-rounded" type="text" name="message" />
                {status === "SUCCESS" ? <p>Thanks!</p> : <button className="pure-button">Submit</button>}
                {status === "ERROR" && <p>Ooops! There was an error.</p>}
              </div>
            </div>
          </fieldset>
        </form>
      </div>
    );
  }

  submitForm(ev) {
    ev.preventDefault();
    const form = ev.target;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader("Accept", "application/json");
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        this.setState({ status: "SUCCESS" });
      } else {
        this.setState({ status: "ERROR" });
      }
    };
    xhr.send(data);
  }
}
